<h1 id="composer" tabindex="-1"><a class="header-anchor" href="#composer" aria-hidden="true">#</a> Composer</h1>
<p><img src="https://getcomposer.org/img/logo-composer-transparent.png" alt="composer logo"></p>
<p>Composer est un logiciel gestionnaire de dépendances libre écrit en PHP. Il permet à ses utilisateurs de déclarer et d'installer les bibliothèques dont le projet principal a besoin.</p>
<h2 id="mise-a-jour-de-dependances-avec-composer" tabindex="-1"><a class="header-anchor" href="#mise-a-jour-de-dependances-avec-composer" aria-hidden="true">#</a> Mise à jour de dépendances avec composer</h2>
<p>La mise à jour se fait via deux commandes :  <code>composer install</code> et <code>composer update</code>.<br>
<code>composer install</code> permet de faire l'installation des dépendances et de garantir leur intégrité, cela permet aussi d'avoir les même versions dans le cas de projets en équipe.<br>
<code>composer update</code> permet d'installer les dernière versions des packets, par contre dans ce cas la on est exposé aux bugs.</p>
